<template>
  <div class="message-page">
    <h2>/</h2>
    
    <div class="input-section">
      <textarea v-model="inputText" placeholder=""></textarea>
      <input type="password" v-model="password" placeholder="">
      
      <div class="button-group">
        <button @click="encryptText" :disabled="isLoading"></button>
        <button @click="decryptText" :disabled="isLoading"></button>
        <button @click="clearAll" :disabled="isLoading"></button>
      </div>
    </div>
    
    <div class="output-section">
      <h3>:</h3>
      <div class="output-box">{{ outputText }}</div>
      <button v-if="outputText" @click="copyToClipboard" class="copy-button">{{ copyButtonText }}</button>
    </div>
  </div>
</template>

<script>
import { encrypt_data, decrypt_data } from 'simple-data-crypto/builder'

export default {
  name: 'MessagePage',
  data() {
    return {
      inputText: '',
      password: '',
      outputText: '',
      copyButtonText: '',
      isLoading: false
    }
  },
  methods: {
    async encryptText() {
      if (!this.inputText || !this.password) {
        this.outputText = ('')
        return
      }
      
      try {
        this.isLoading = true
        this.outputText = '...'
        this.outputText = await encrypt_data(this.inputText, this.password)
      } catch (error) {
        this.outputText = ': ' + error.message
      } finally {
        this.isLoading = false
      }
    },
    async decryptText() {
      if (!this.inputText || !this.password) {
        this.outputText = ('')
        return
      }
      
      try {
        this.isLoading = true
        this.outputText = '...'
        this.outputText = await decrypt_data(this.inputText, this.password)
      } catch (error) {
        this.outputText = ': ' + error.message
      } finally {
        this.isLoading = false
      }
    },
    clearAll() {
      this.inputText = ''
      this.password = ''
      this.outputText = ''
    },
    async copyToClipboard() {
      if (!this.outputText) return;
      
      const originalText = this.copyButtonText;
      try {
        await navigator.clipboard.writeText(this.outputText);
        this.copyButtonText = '!';
      } catch (err) {
        this.copyButtonText = '!';
      }
      setTimeout(() => {
        this.copyButtonText = originalText;
      }, 1500);
    }
  }
}
</script>

<style scoped>
.message-page {
  max-width: 800px;
  margin: 0 auto;
  padding: 10px;
}

.input-section {
  margin-bottom: 20px;
}

textarea {
  width: 100%;
  height: 150px;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

input[type="password"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.button-group {
  display: flex;
  gap: 10px;
}

button {
  flex: 1;
  padding: 10px;
  background: #2c3e50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:disabled {
  background: #cccccc;
  cursor: not-allowed;
}

button:not(:disabled):hover {
  background: #1a2a3a;
}

.output-section {
  margin-top: 20px;
}

.output-box {
  min-height: 100px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: #f9f9f9;
  white-space: pre-wrap;
  word-break: break-all;
}
</style>